    outline: none;
    width: 100%;
    border: 1px solid grey;
    font-size: 1rem;
    padding: 1rem;
    border-radius: .5rem;
    margin : 2rem 2rem 2rem 0;
}
.std_ResearcherCard{
    align-self: center;
    margin : 3rem 3rem 3rem 3rem;
