    width : 250px;
    box-shadow: 0 0 28px 4 px rgba(0,0,0,0.1);
    transition: -webkit-transform .3s;
    transition: transform .3s;
    transition: transform .3s, -webkit-transform .3s;
    -webkit-animation: ease-in;
            animation: ease-in;
    cursor: pointer;
